.container {
  width: 80%;
  margin-inline: auto;
  position: relative;
  background-color: #f0f7c4;
  border-radius: 24px;
  padding: 12px 4%;
  z-index: 1;
  overflow: hidden;
  padding-bottom: 0;

  .content-wrapper {
    .section-title {
      font-size: var(--title-font);
      font-weight: 600;
      margin-top: 24px;
    }
  }

  .image-wrapper {
    width: 60%;
    position: absolute;
    inset-inline-end: 4%;
    display: flex;
    justify-content: end;
    z-index: -1;
    bottom: 0;
    left: 0;

    img {
      width: auto;
      height: 40%;
      object-fit: contain;
    }
  }
}
@media screen and (max-width: 900px) {
  .container {
    .image-wrapper {
      display: none;
    }
  }
}
